"use client";
import React from "react";

type LinkItem = {
    code: string;
    url: string;
    clicks: number;
    lastClicked?: string | null;
};

type Props = {
    links?: LinkItem[] | null;
    onDelete?: (code: string) => Promise<void> | void;
};

const truncate = (s: string, n = 60) =>
    s.length > n ? s.slice(0, n - 1) + "…" : s;

const UrlList: React.FC<Props> = ({ links, onDelete }) => {
    const items = links ?? [];

    const handleCopy = async (code: string) => {
        try {
            const short = `${window.location.origin}/${code}`;
            await navigator.clipboard.writeText(short);
            // silent success — could add toast
        } catch {
            // ignore clipboard errors
        }
    };

    const handleDelete = async (code: string) => {
        if (!confirm(`Delete short code "${code}"? This cannot be undone.`)) return;
        try {
            const res = await fetch(`/api/links/${code}`, { method: "DELETE" });
            if (res.ok) {
                if (onDelete) await onDelete(code);
            } else {
                const err = await res.json().catch(() => ({}));
                alert(err?.error || "Delete failed");
            }
        } catch {
            alert("Network error");
        }
    };

    if (!items.length) {
        return (
            <div className="p-4 text-sm text-gray-600">
                No links yet. Use the form to create your first short link.
            </div>
        );
    }

    return (
        <div className="overflow-x-auto">
            <table className="min-w-full divide-y">
                <thead>
                    <tr>
                        <th className="text-left px-3 py-2">Code</th>
                        <th className="text-left px-3 py-2">Target URL</th>
                        <th className="text-left px-3 py-2">Clicks</th>
                        <th className="text-left px-3 py-2">Last clicked</th>
                        <th className="px-3 py-2">Actions</th>
                    </tr>
                </thead>
                <tbody>
                    {items.map((l) => (
                        <tr key={l.code} className="border-t">
                            <td className="px-3 py-2 font-mono text-sm">
                                <a href={`/${l.code}`} className="text-blue-600 hover:underline">
                                    {l.code}
                                </a>
                            </td>
                            <td className="px-3 py-2 max-w-xl">
                                <a
                                    href={l.url}
                                    target="_blank"
                                    rel="noreferrer"
                                    className="block text-sm text-gray-700 hover:underline"
                                    title={l.url}
                                >
                                    {truncate(l.url, 80)}
                                </a>
                            </td>
                            <td className="px-3 py-2">{l.clicks}</td>
                            <td className="px-3 py-2 text-sm text-gray-500">
                                {l.lastClicked ? new Date(l.lastClicked).toLocaleString() : "—"}
                            </td>
                            <td className="px-3 py-2 space-x-2">
                                <button
                                    onClick={() => handleCopy(l.code)}
                                    className="px-2 py-1 border rounded text-sm"
                                >
                                    Copy
                                </button>
                                <button
                                    onClick={() => handleDelete(l.code)}
                                    className="px-2 py-1 bg-red-600 text-white rounded text-sm"
                                >
                                    Delete
                                </button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default UrlList;